*{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}

.container {
    position: absolute;
    width: 100%;
    min-height: 100vh;
    padding: 10px 18%;
    pointer-events: none;
}

nav {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    pointer-events: all;
}

.logo {
    width: 96px;
    cursor: pointer;
}

nav ul {
    list-style: none;
    width: 100%;
    text-align: right;
    padding-right: 60px;
}

nav ul li {
    display: inline-block;
    margin: 10px 20px;
}

nav ul li a {
    color: #E3EAB1;
    text-decoration: none;
}

.btn {
    justify-content: center;
    width: 10%;
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border: 0;
    outline: 0;
    border-radius: 5px;
    background: #FDDB57;
    color: #fff;
    font-weight: 500;
    cursor: pointer;
}